import React from 'react';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import RefreshOutlinedIcon from '@material-ui/icons/RefreshOutlined';
import ExitToAppOutlinedIcon from '@material-ui/icons/ExitToAppOutlined';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';
import AppChart from './AppChart';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            flexGrow: 1,
            backgroundColor: 'red',
        },
        menuButton: {
            marginRight: theme.spacing(2),
        },
        title: {
            flexGrow: 1,
        },
        segments: {
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            '& > *': {
                margin: theme.spacing(1),
            },
        },
    }),
);

export default function Landscape(props: any) {
    const [alignment, setAlignment] = React.useState<number>(0);
    const classes = useStyles();

    const handleAlignment = (event: React.MouseEvent<HTMLElement>, newAlignment: number) => {
        setAlignment(newAlignment);
    };

    return (
        <div>
        <div className={classes.root}>
            <AppBar position="static">
                <Toolbar>
                    <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                            <RefreshOutlinedIcon />  Reload
                    </IconButton>
                    
                        <div className={classes.segments}>
                            <ToggleButtonGroup
                                value={alignment}
                                exclusive
                                onChange={handleAlignment}
                            >
                                <ToggleButton value={0} >CURVE </ToggleButton>
                                <ToggleButton value={1} >BAR</ToggleButton>
                                <ToggleButton value={2} >BUBBLE</ToggleButton>
                                <ToggleButton value={3} >PIE</ToggleButton>
                            </ToggleButtonGroup>

                     
                        </div>
                        <Typography variant="h6" className={classes.title} />
                        <Button color="inherit">{props.username}</Button>
                        <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu" onClick={props.logoutAction}>
                        <ExitToAppOutlinedIcon />
                    </IconButton>
                </Toolbar>
            </AppBar>
        </div>
            <AppChart value={alignment}/>
            </div>
    );
}
